.card {
    border-radius: 2px;
    height: 250px;
    margin: 1rem;
    position: relative;
    width: 250px;
    border-color: black;
    border-style: solid;
    border-width: 10px;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    background:white;
  }
  .card > .img-container > img {
    width: 60%
  }

  
.navbar {
    width: 100%;
    font-size: 60px;
    text-align: center;
    color: rgb(19, 16, 16);
    background-color: rgb(246, 250, 11);
    height: 120px;
    font-family: Arial, Helvetica, sans-serif;
    top: 0;
    margin-top: -10px;
    text-align: center;
    padding:20px;
    border-radius: 25px;
    border: 4px solid black;
  }
.wrapper {
    padding-top: 50px;
    /* background: #ECEFF1; */
    background-image: url("https://wallpapertag.com/wallpaper/full/c/4/7/526650-best-despicable-me-minions-background-2880x1800-image.jpg");
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding-bottom: 50px;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }
  
.intro {
    position: inherit;
    width: 100%;
    text-align: center;
    padding:10px;
    border-radius: 25px;
    margin:0px;
    color: white;
    background-color: black;
}


* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}

